import React, { Component } from 'react';
import Navbar from '../components/Navbar.jsx';
import Footer from '../components/Footer.jsx';
import Jumbotron from '../components/Jumbotron.jsx';
import { Link } from 'react-router-dom';

class Home extends Component {
  render() {
    return (
      <div>
      <Navbar />
       <Jumbotron title="HOME OF SMART" />
        <div className="container-fluid">
         <div className="row text-center">
          <div className="col">
          <Link to="studenten" style = {{textDecoration:'none'}}>
           <button type="button" className="btn btn-outline-info btn-lg btn-block mb-3 text-truncate"><span className="d-inline-block">Ik ben een student en zoek informatie</span></button>
          </Link>
          </div>
          <div className="col">
          <Link to="bedrijven" style = {{textDecoration:'none'}}>
           <button type="button" className="btn btn-outline-danger btn-lg btn-block mb-3 text-truncate"><span className="d-inline-block">Ik ben een bedrijf en wil samenwerken</span></button>
          </Link>
          </div>
         </div>
        </div>
      <Footer />
      </div>
    );
  }
}

export default Home;



