import React, { Component } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';

import Home from './pages/Home.jsx';
import Minor from './pages/Minor.jsx';
import Team from './pages/Team.jsx';
import Inschrijven from './pages/Inschrijven.jsx';
import Bedrijven from './pages/Bedrijven.jsx';
import Studenten from './pages/Studenten.jsx';
import Cases from './pages/Cases.jsx';
import Forum from './pages/Forum.jsx';

class App extends Component {
  render() {
    return (
     <Router>
       <div className="container">
         <Route exact path="/" component={Home}/>
         <Route path="/team" component={Team}/>
         <Route path="/studenten" component={Studenten}/>
         <Route path="/minor" component={Minor}/>
         <Route path="/inschrijven" component={Inschrijven}/>
         <Route path="/bedrijven" component={Bedrijven}/>
         <Route path="/cases" component={Cases}/>
         <Route path="/forum" component={Forum}/>
       </div>
     </Router>
    );
  }
}

export default App;



